<template>
<div>
    <el-form :model="objFormSingle" :rules="rules" ref="objFormSingle" label-width="150px">
        <el-form-item label="平台" prop="platform">
            <el-select v-model="objFormSingle.platform" placeholder="请选择平台">
                <el-option v-for="(ele,idx) in aPlatform" :label="ele.name" :value="ele.id" :key="idx"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="名称" prop="name">
            <el-input v-model="objFormSingle.name" placeholder="请输入名称"></el-input>
        </el-form-item>
        <el-form-item label="分类信息" prop="describe">
            <el-input v-model="objFormSingle.describe" placeholder="请输入分类信息"></el-input>
        </el-form-item>
        <el-form-item label="生成数量" prop="num">
            <el-input-number v-model="objFormSingle.num" controls-position="right" :min="1"></el-input-number>
        </el-form-item>
        <el-form-item label="使用最低等级" prop="minLevel">
            <el-input v-model="objFormSingle.minLevel" placeholder="请输入使用最低等级"></el-input>
        </el-form-item>
        <el-form-item label="使用最高等级" prop="maxLevel">
            <el-input v-model="objFormSingle.maxLevel" placeholder="请输入使用最高等级"></el-input>
        </el-form-item>
        <el-form-item label="生效日期" prop="start">
            <el-date-picker v-model="objFormSingle.start" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="选择生效日期"></el-date-picker>
        </el-form-item>
        <el-form-item label="失效日期" prop="end">
            <el-date-picker v-model="objFormSingle.end" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="选择失效日期"></el-date-picker>
        </el-form-item>
        <el-form-item label="邮件标题" prop="title">
            <el-input v-model="objFormSingle.title" placeholder="请输入内容"></el-input>
        </el-form-item>
        <el-form-item label="邮件内容" prop="content">
            <el-input type="textarea" v-model="objFormSingle.content" placeholder="请输入内容"></el-input>
        </el-form-item>
        <el-form-item label="礼包" prop="couponId">
            <el-select v-model="objFormSingle.couponId" placeholder="请选择礼包">
                <el-option v-for="(ele,idx) in aCoupon" :label="ele.name" :value="ele.id" :key="idx"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="分发客户端" prop="channels">
            <el-checkbox-group v-model="objFormSingle.channels">
                <el-checkbox v-for="(item,idx) in aChannels" :label="item.id+''" :key="idx">{{item.name}}</el-checkbox>
            </el-checkbox-group>
        </el-form-item>
        <el-form-item label="礼包类型">
            <el-radio-group v-model="objFormSingle.type" prop="type">
                <p>
                    <el-radio label="1">普通类型</el-radio>
                    <span>1个礼包码，1个账号只能使用1次、该类礼包1个账号只能使用其中1个</span>
                </p>
                <p>
                    <el-radio label="2">万能类型</el-radio>
                    <span>1个礼包码，N个账号只能使用1次、该类礼包1个账号只能使用其中1个</span>
                </p>
                <p>
                    <el-radio label="3">福利类型</el-radio>
                    <span>1个礼包码，1个账号只能使用1次、该类礼包1个账号只能使用其中N个</span>
                </p>
            </el-radio-group>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="submitForm('objFormSingle')">创建礼包码</el-button>
        </el-form-item>
    </el-form>
</div>
</template>

<script>
export default {
    name: "coupon-createCode",
    props: {
        formData: {
            type: Object
        }
    },
    data() {
        var checkServer = (rule, value, callback) => {
            if (!value) {
                return callback(new Error("至少选择一个服务器"));
            } else {
                callback();
            }
        };
        return {
            aCoupon: [{
                    id: "123",
                    name: "礼包1"
                },
                {
                    id: "124",
                    name: "礼包2"
                },
                {
                    id: "125",
                    name: "礼包3"
                }
            ],
            // 渠道
            aChannels: [],
            aPlatform: [],
            objFormSingle: {},
            rules: {
                platform: [{
                    required: true,
                    message: "请选择平台",
                    trigger: "change"
                }],
                name: [{
                    required: true,
                    message: "请输入名称",
                    trigger: "blur"
                }],
                describe: [{
                    required: true,
                    message: "请输入分类信息",
                    trigger: "blur"
                }],
                num: [{
                    required: true,
                    message: "请输入数量"
                }],
                minLevel: [{
                    required: true,
                    message: "请输入使用最低等级",
                    trigger: "blur"
                }],
                maxLevel: [{
                    required: true,
                    message: "请输入使用最高等级",
                    trigger: "blur"
                }],
                start: [{
                    required: true,
                    message: "请选择时间",
                    trigger: "change"
                }],
                end: [{
                    required: true,
                    message: "请选择时间",
                    trigger: "change"
                }],
                title: [{
                    required: true,
                    message: "请输入邮件标题",
                    trigger: "blur"
                }],
                content: [{
                    required: true,
                    message: "请输入邮件内容",
                    trigger: "blur"
                }],
                couponId: [{
                    required: true,
                    message: "请选择礼包",
                    trigger: "change"
                }],
                channels: [{
                    required: true,
                    message: "请选择分发客户端",
                    trigger: "change"
                }],
                type: [{
                    required: true,
                    message: "请选择礼包类型",
                    trigger: "change"
                }]
            }
        };
    },
    watch: {
        formData: {
            handler() {
                let objCopy = JSON.parse(JSON.stringify(this.formData));
                objCopy.platform = this.formData.platform.id;
                objCopy.type += "";
                this.objFormSingle = objCopy;
            }
        },
        deep: true
    },
    methods: {
        //   提交
        submitForm(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    let objCopy = JSON.parse(JSON.stringify(this.objFormSingle));
                    this.$fetch
                        .post(API.coupon.create_coupon_code, objCopy)
                        .then(response => {
                            let res = response.data;
                            if (res.status == 200) {
                                this.$emit("submitUpdate");
                                this.$refs[formName].resetFields();
                                this.$message.success(`修改成功 礼包批次号为：【${res.data.sequenceId}】`)
                            } else {
                                this.$message.error(res.desc);
                            }
                        });
                } else {
                    return false;
                }
            });
        },
        fetchPlatformList() {
            this.$fetch.post(API.coupon.platform_list).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.aPlatform = res.data;
                } else {
                    this.$message.error(res.desc);
                }
            });
        },
        fetchChannelList() {
            this.$fetch.post(API.anno.channels).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.aChannels = res.data;
                } else {
                    this.$message.error(res.desc);
                }
            });
        },
        fetchCouponIdList() {
            this.$fetch.post(API.coupon.coupon_id_list).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.aCoupon = res.data;
                } else {
                    this.$message.error(res.desc);
                }
            });
        }
    },
    mounted() {
        this.fetchChannelList();
        this.fetchPlatformList();
        this.fetchCouponIdList();
        let objCopy = JSON.parse(JSON.stringify(this.formData));
        objCopy.platform = this.formData.platform.id;
        objCopy.type += "";
        this.objFormSingle = objCopy;
    }
};
</script>
