<template>
<div>
    <el-form :model="objForm" :rules="rules" ref="objForm" label-width="100px">
        <el-form-item label="礼包ID" prop="id">
            <el-input v-model="objForm.id" placeholder="请输入礼包ID"></el-input>
        </el-form-item>
        <el-form-item label="礼包内容">
            <el-button @click.prevent="addItem">添加</el-button>
        </el-form-item>
        <el-row style="marginTop:10px;" :gutter="20" v-for="(item, index) in objForm.items" :key="index">
            <el-col :span="1" :offset="2">
                <span style="lineHeight:40px;">{{index+1}}</span>
            </el-col>
            <el-col :span="6">
                <el-form-item label="道具" :prop="'items.' + index + '.itemId'" label-width="60px" :rules="{
      required: true, message: '道具不能为空', trigger: 'blur'
    }">
                    <el-select v-model="item.itemId" filterable placeholder="请选择道具">
                        <el-option v-for="(ele,idx) in aItemList" :label="ele.type+'-'+ele.name" :value="ele.itemId" :key="idx"></el-option>
                    </el-select>
                </el-form-item>
            </el-col>
            <el-col :span="6">
                <el-form-item label="数量" :prop="'items.' + index + '.num'" label-width="60px" :rules="{
      required: true, message: '数量不能为空', trigger: 'blur'
    }">
                    <el-input-number v-model="item.num" controls-position="right" :min="1"></el-input-number>
                </el-form-item>
            </el-col>
            <el-col :span="4">
                <el-button @click.prevent="removeItem(item)">删除</el-button>
            </el-col>
        </el-row>
        <el-form-item>
            <el-button type="primary" @click="submitForm('objForm')">创建礼包ID</el-button>
        </el-form-item>
    </el-form>
</div>
</template>

<script>
export default {
    name: "create-id",
    props: {
        oriTab: {
            type: String,
            default: "create"
        }
    },
    data() {
        return {
            aItemList: [],
            objForm: {
                id: "",
                items: [{
                    itemId: "",
                    num: 1
                }]
            },
            rules: {
                id: [{
                    required: true,
                    message: "请输入id",
                    trigger: "blur"
                }]
            }
        };
    },
    watch: {
        oriTab: {
            handler() {
                if (this.oriTab == "createId") {
                    this.fetchItemList();
                }
            }
        }
    },
    methods: {
        removeItem(item) {
            var index = this.objForm.items.indexOf(item);
            if (index !== -1) {
                this.objForm.items.splice(index, 1);
            }
        },
        addItem() {
            this.objForm.items.push({
                itemId: "",
                num: 1
            });
        },

        //   提交
        submitForm(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    let objCopy = JSON.parse(JSON.stringify(this.objForm));
                    this.$fetch
                        .post(API.coupon.create_coupon_id, objCopy)
                        .then(response => {
                            let res = response.data;
                            if (res.status == 200) {
                                this.$refs[formName].resetFields();
                                this.$message.success(res.desc)
                            } else {
                                this.$message.error(res.desc);
                            }
                        });
                } else {
                    return false;
                }
            });
        },

        fetchItemList() {
            this.$fetch.post(API.email.item_list).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.aItemList = res.data;
                } else {
                    this.$message.error(res.desc);
                }
            });
        }
    },
    mounted() {
        this.fetchItemList();
    }
};
</script>
