<template>
<div>
    <!-- 搜索 -->

    <div>
        <el-form :model="objSearch" label-width="100px">
            <el-row :gutter="20">
                <el-col :span="7">
                    <el-form-item label="CD-KEY">
                        <el-input v-model="objSearch.cdkey" placeholder="请输入礼包ID"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="3">
                    <el-button type="primary" @click="handleSearch">搜索</el-button>
                </el-col>
            </el-row>
        </el-form>
    </div>
    <!-- 表格 -->
    <div>
        <el-table :data="aTableData" tooltip-effect="dark" style="width: 100%">
            <el-table-column prop="cdkey" label="礼包cdkey" min-width="100"></el-table-column>
            <el-table-column prop="platform" label="平台" min-width="100" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{scope.row.platform.name}}</span>
                </template>
            </el-table-column>
            <el-table-column prop="type" label="礼包类型" min-width="100" show-overflow-tooltip></el-table-column>
            <el-table-column prop="couponId" label="礼包id" min-width="100" show-overflow-tooltip></el-table-column>
            <el-table-column prop="describe" label="分类信息" min-width="100" show-overflow-tooltip></el-table-column>
            <el-table-column prop="isUse" label="使用状态" min-width="100" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{scope.row.user?'是':'否'}}</span>
                </template>
            </el-table-column>
            <el-table-column prop="useTime" label="使用时间" min-width="100" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{scope.row.useTime}}</span>
                </template>
            </el-table-column>
            <el-table-column prop="serverId" label="服务器id" min-width="100" show-overflow-tooltip></el-table-column>
            <el-table-column prop="pid" label="角色id 数组" min-width="100" show-overflow-tooltip></el-table-column>
            <!-- <el-table-column prop="num" label="物品数量" min-width="100" show-overflow-tooltip></el-table-column>
          <template slot-scope="scope">
            <span>{{scope.row.num-scope.row.useNum}}</span>
          </template>
        </el-table-column> -->
        </el-table>
    </div>
</div>
</template>

<script>
export default {
    name: "id-list",
    props: {
        oriTab: {
            type: String,
            default: "create"
        }
    },
    data() {
        return {
            objSearch: {
                cdkey: '' //ZR534
            },
            aTableData: []
        };
    },
    watch: {
        oriTab: {
            handler() {
                if (this.oriTab == "idList") {
                    this.fetchCouponIdList();
                }
            }
        }
    },
    methods: {
        //   搜索
        handleSearch() {
            this.fetchCouponIdList();
        },
        // 列表
        fetchCouponIdList() {
            let params = JSON.parse(JSON.stringify(this.objSearch));
            this.$fetch.post(API.coupon.select_cdkey, params).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.aTableData = [res.data];
                } else {
                    this.$message.error(res.desc);
                }
            });
        }
    },
    mounted() {
        // this.fetchCouponIdList()
    }
};
</script>

<style lang="stylus" scoped>
.pagination {
    text-align: right;
    margin-top: 15px;
}
</style>
