<template>
<div>
    <!-- 搜索 -->
    <div>
        <el-form :model="objSearch" label-width="100px">
            <el-row :gutter="20">
                <el-col :span="7">
                    <el-form-item label="礼包" prop="couponId">
                        <el-select v-model="objSearch.couponId" placeholder="请选择礼包">
                            <el-option v-for="(ele,idx) in aCouponId" :label="ele.id" :value="ele.id" :key="idx"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="3">
                    <el-button type="primary" @click="handleSearch">搜索</el-button>
                </el-col>
            </el-row>
        </el-form>
    </div>
    <div v-show="!isRes">
        <p style="textAlign:center;">暂无搜索结果！</p>
    </div>
    <div v-show="isRes">
        <el-form :model="objForm" :rules="rules" ref="objForm" label-width="100px">
            <el-form-item label="礼包内容">
                <el-button @click.prevent="addItem">添加</el-button>
            </el-form-item>
            <el-row style="marginTop:10px;" :gutter="20" v-for="(item, index) in objForm.items" :key="index">
                <el-col :span="1" :offset="2">
                    <span style="lineHeight:40px;">{{index+1}}</span>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="道具" :prop="'items.' + index + '.itemId'" label-width="60px" :rules="{
      required: true, message: '道具不能为空', trigger: 'blur'
    }">
                        <el-select v-model="item.itemId" filterable placeholder="请选择道具">
                            <el-option v-for="(ele,idx) in aItemList" :label="ele.type+'-'+ele.name" :value="ele.itemId" :key="idx"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="数量" :prop="'items.' + index + '.num'" label-width="60px" :rules="{
      required: true, message: '数量不能为空', trigger: 'blur'
    }">
                        <el-input-number v-model="item.num" controls-position="right" :min="1"></el-input-number>
                    </el-form-item>
                </el-col>
                <el-col :span="4">
                    <el-button @click.prevent="removeItem(item)">删除</el-button>
                </el-col>
            </el-row>
            <el-form-item>
                <el-button type="primary" @click="submitForm('objForm')">修改礼包ID</el-button>
            </el-form-item>
        </el-form>
    </div>
</div>
</template>

<script>
export default {
    name: "create-id",
    props: {
        oriTab: {
            type: String,
            default: "create"
        }
    },
    data() {
        return {
            aCouponId: [],
            isRes: false,
            objSearch: {
                couponId: ""
            },

            aItemList: [],
            objForm: {
                id: "",
                items: []
            },
            rules: {
                id: [{
                    required: true,
                    message: "请输入id",
                    trigger: "blur"
                }]
            }
        };
    },
    watch: {
        oriTab: {
            handler() {
                if (this.oriTab == "idUpdate") {
                    this.fetchCouponIdList();
                    this.fetchItemList();
                }
            }
        }
    },
    methods: {
        //   搜索
        handleSearch() {
            if (this.objSearch.couponId) {
                this.isRes = true;
                this.fetchList();
            } else {
                this.$message.erroe('请先选择礼包！')
            }
        },
        // 列表
        fetchList() {
            let params = JSON.parse(JSON.stringify(this.objSearch));
            this.$fetch.post(API.coupon.select_coupon_id, params).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.objForm = {
                        id: this.objSearch.couponId,
                        items: res.data
                    }
                } else {
                    this.$message.error(res.desc);
                }
            });
        },
        removeItem(item) {
            var index = this.objForm.items.indexOf(item);
            if (index !== -1) {
                this.objForm.items.splice(index, 1);
            }
        },
        addItem() {
            this.objForm.items.push({
                itemId: "",
                num: 1
            });
        },
        //   提交
        submitForm(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    let objCopy = JSON.parse(JSON.stringify(this.objForm));
                    this.$fetch
                        .post(API.coupon.update_coupon_id, objCopy)
                        .then(response => {
                            let res = response.data;
                            if (res.status == 200) {
                                this.$refs[formName].resetFields();
                                this.$message.success(res.desc)
                            } else {
                                this.$message.error(res.desc);
                            }
                        });
                } else {
                    return false;
                }
            });
        },
        fetchItemList() {
            this.$fetch.post(API.email.item_list).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.aItemList = res.data;
                } else {
                    this.$message.error(res.desc);
                }
            });
        },
        fetchCouponIdList() {
            this.$fetch.post(API.coupon.coupon_id_list).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    let result = res.data;
                    result.forEach(item => {
                        item.itemId = item.itemId + '';
                    })
                    this.aCouponId = result;
                } else {
                    this.$message.error(res.desc);
                }
            });
        }
    },
    mounted() {
        this.fetchCouponIdList();
        this.fetchItemList();
    }
};
</script>

<style lang="stylus" scoped>
.pagination {
    text-align: right;
    margin-top: 15px;
}
</style>
