<template>
<div>
    <!-- 搜索 -->
    <div>
        <el-form :model="objSearch" label-width="100px">
            <el-row :gutter="20">
                <el-col :span="7">
                    <el-form-item label="礼包批次号">
                        <el-input v-model="objSearch.sequenceId" placeholder="请输入礼包批次号"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="7">
                    <el-form-item label="平台">
                        <el-input v-model="objSearch.platform" placeholder="请输入平台"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="礼包类型">
                        <el-input v-model="objSearch.type" placeholder="请输入礼包类型"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" @click="handleSearch">搜索</el-button>
                    <el-button type="primary" @click="handleReset">重置</el-button>
                </el-col>
            </el-row>
        </el-form>
    </div>
    <!-- 表格 -->
    <div>
        <el-table :data="aTableData" tooltip-effect="dark" style="width: 100%">
            <el-table-column prop="sequenceId" label="礼包批次号" min-width="100"></el-table-column>
            <el-table-column prop="type" label="礼包类型" min-width="80" show-overflow-tooltip></el-table-column>
            <el-table-column prop="couponId" label="礼包ID" min-width="100" show-overflow-tooltip></el-table-column>
            <el-table-column prop="describe" label="分类信息" min-width="100" show-overflow-tooltip></el-table-column>
            <el-table-column prop="end" label="过期时间" min-width="100" show-overflow-tooltip></el-table-column>
            <el-table-column prop="appendTime" label="追加时间" min-width="100" show-overflow-tooltip></el-table-column>
            <el-table-column prop="num" label="总量" min-width="70" show-overflow-tooltip></el-table-column>
            <el-table-column prop="useNum" label="已使用" min-width="70" show-overflow-tooltip></el-table-column>
            <el-table-column prop="noUseNum" label="未使用" min-width="70" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{scope.row.num-scope.row.useNum}}</span>
                </template>
            </el-table-column>
            <el-table-column label="操作" min-width="250">
                <template slot-scope="scope">
                    <el-button @click.native.prevent="handleClickUpdate(scope.row)" type="text" size="small">修改</el-button>
                    <el-button @click.native.prevent="handleClickTime(scope.row)" type="text" size="small">追加时间</el-button>
                    <el-button @click.native.prevent="handleClickNum(scope.row)" type="text" size="small">追加数量</el-button>
                    <el-button @click.native.prevent="handleClickDownload(scope.row)" type="text" size="small">下载</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- 分页 -->
        <el-pagination class="pagination" background layout="prev, pager, next" @current-change="handlePageChange" :current-page.sync="objPager.currentPage" :page-size.sync="objPager.pageSize" :total="objPager.total"></el-pagination>
    </div>

    <!-- 修改 -->
    <el-dialog title="修改礼包码" :visible.sync="bDialogUpdate" width="800px">
        <code-update :formData="objSingle" @submitUpdate="handleClickSubmitUpdate" />
    </el-dialog>
    <!-- 追加时间 -->
    <el-dialog title="追加时间" :visible.sync="bDialogTime" width="400px">
        <el-form :model="objTime" :rules="objRule" label-width="100px">
            <el-form-item label="礼包批次号">
                <p>{{objTime.sequenceId}}</p>
            </el-form-item>
            <el-form-item label="追加时间" prop="appendTime">
                <el-date-picker v-model="objTime.appendTime" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="选择追加时间"></el-date-picker>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="handleClickSubmitTime">确认追加</el-button>
            </el-form-item>
        </el-form>
    </el-dialog>
    <!-- 追加数量 -->
    <el-dialog title="追加数量" :visible.sync="bDialogNum" width="400px">
        <el-form :model="objNum" label-width="100px">
            <el-form-item label="礼包批次号">
                <p>{{objNum.sequenceId}}</p>
            </el-form-item>
            <el-form-item label="追加数量">
                <el-input-number v-model="objNum.appendNum" controls-position="right" :min="1"></el-input-number>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="handleClickSubmitNum">确认追加</el-button>
            </el-form-item>
        </el-form>
    </el-dialog>
</div>
</template>

<script>
import codeUpdate from "./codeUpdate";
import axios from "axios";
import baseURL from "../../config/baseUrl";
export default {
    name: "create-id",
    components: {
        codeUpdate
    },
    props: {
        oriTab: {
            type: String,
            default: "create"
        }
    },
    data() {
        return {
            bDialogUpdate: false,
            bDialogTime: false,
            bDialogNum: false,
            objSearch: {},
            objSingle: {},
            objTime: {},
            objNum: {},
            objRule: {
                appendTime: [{
                    required: true,
                    message: "请选择时间",
                    trigger: "change"
                }]
            },
            aTableData: [],
            // 节点分页数据
            objPager: {
                total: 20,
                currentPage: 1,
                pageSize: 10
            }
        };
    },
    watch: {
        oriTab: {
            handler() {
                if (this.oriTab == "codeShow") {
                    this.fetchCouponList();
                }
            }
        }
    },
    methods: {
        // 重置
        handleReset() {
            this.objSearch = {};
        },
        //   搜索
        handleSearch() {
            this.fetchCouponList();
        },

        // 分页
        handlePageChange(e) {
            this.objPager.currentPage = e;
            this.fetchCouponList();
        },
        // 修改
        handleClickUpdate(params) {
            this.objSingle = JSON.parse(JSON.stringify(params));
            if (!this.objSingle.channels) {
                this.objSingle.channels = [];
            } else {

                let arr = [];
                params.channels.forEach(item => {
                    arr.push(item.id + '');
                });
                this.objSingle.channels = arr;
            }
            this.bDialogUpdate = true;
        },
        // 确认修改
        handleClickSubmitUpdate() {
            this.bDialogUpdate = false;
            this.fetchCouponList();
        },
        // 追加时间
        handleClickTime(params) {
            this.objTime.sequenceId = params.sequenceId;
            this.bDialogTime = true;
        },
        // 确认追加时间
        handleClickSubmitTime() {
            let params = JSON.parse(JSON.stringify(this.objTime));
            this.$fetch.post(API.coupon.coupon_append_time, params).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.bDialogTime = false;
                    this.$message.success(res.desc);
                } else {
                    this.$message.error(res.desc);
                }
            });
        },
        // 追加数量
        handleClickNum(params) {
            this.objNum.sequenceId = params.sequenceId;
            this.objNum.appendNum = 1;
            this.bDialogNum = true;
        },
        // 确认追加数量
        handleClickSubmitNum() {
            let params = JSON.parse(JSON.stringify(this.objNum));
            this.$fetch.post(API.coupon.coupon_append_num, params).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.bDialogNum = false;
                    this.$message.success(res.desc);
                } else {
                    this.$message.error(res.desc);
                }
            });
        },
        // 下载
        handleClickDownload(data) {
            let params = {
                sequenceId: data.sequenceId
            };
            axios({
                method: "post",
                headers: {
                    "content-type": "application/json; charset=utf-8"
                },
                responseType: "stream",
                url: baseURL + API.coupon.get_cdkey,
                data: params
            }).then(response => {
                console.log("response :", response);
                let dataBlob = new Blob([`\ufeff${response.data}`], {
                    type: "text/plain;charset=utf-8"
                });

                const url = window.URL.createObjectURL(dataBlob);

                const a = document.createElement("a");
                a.href = url;
                a.download = `礼包批次${data.sequenceId}.csv`;
                a.click();
                window.URL.revokeObjectURL(url);
            });
        },
        // 列表
        fetchCouponList() {
            let params = JSON.parse(JSON.stringify(this.objSearch));
            params = {
                ...params,
                page: this.objPager.currentPage,
                pageSize: this.objPager.pageSize
            };
            this.$fetch.post(API.coupon.coupon_list, params).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.aTableData = res.data.coupons;
                    this.objPager.total = res.data.total;
                } else {
                    this.$message.error(res.desc);
                }
            });
        }
    },
    mounted() {
        this.$on("closeDialog", () => {
            this.bDialogUpdate = false;
        });
        this.fetchCouponList();
    }
};
</script>

<style lang="stylus" scoped>
.pagination {
    text-align: right;
    margin-top: 15px;
}
</style>
