<template>
<div>
    <el-form :model="objFormEmailSingle" :rules="rules" ref="objFormEmailSingle" label-width="150px">
        <el-form-item label="平台" prop="platform">
            <el-select v-model="objFormEmailSingle.platform" placeholder="请选择平台">
                <el-option v-for="(ele,idx) in aPlatform" :label="ele.name" :value="ele.id" :key="idx"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="名称" prop="name">
            <el-input v-model="objFormEmailSingle.name" placeholder="请输入名称"></el-input>
        </el-form-item>
        <el-form-item label="分类信息" prop="describe">
            <el-input v-model="objFormEmailSingle.describe" placeholder="请输入分类信息"></el-input>
        </el-form-item>
        <el-form-item label="生成数量" prop="num">
            <el-input-number v-model="objFormEmailSingle.num" controls-position="right" :min="1"></el-input-number>
        </el-form-item>
        <el-form-item label="使用最低等级" prop="minLevel">
            <el-input v-model="objFormEmailSingle.minLevel" placeholder="请输入使用最低等级"></el-input>
        </el-form-item>
        <el-form-item label="使用最高等级" prop="maxLevel">
            <el-input v-model="objFormEmailSingle.maxLevel" placeholder="请输入使用最高等级"></el-input>
        </el-form-item>
        <el-form-item label="生效日期" prop="start">
            <el-date-picker v-model="objFormEmailSingle.start" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="选择生效日期"></el-date-picker>
        </el-form-item>
        <el-form-item label="失效日期" prop="end">
            <el-date-picker v-model="objFormEmailSingle.end" format="yyyy-MM-dd HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder="选择失效日期"></el-date-picker>
        </el-form-item>
        <el-form-item label="邮件标题" prop="title">
            <el-input v-model="objFormEmailSingle.title" placeholder="请输入邮件标题"></el-input>
        </el-form-item>
        <el-form-item label="邮件内容" prop="content">
            <el-input type="textarea" v-model="objFormEmailSingle.content" placeholder="请输入邮件内容"></el-input>
        </el-form-item>
        <el-form-item label="礼包" prop="couponId">
            <el-select v-model="objFormEmailSingle.couponId" placeholder="请选择礼包">
                <el-option v-for="(ele,idx) in aCouponId" :label="ele.id" :value="ele.id" :key="idx"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="分发客户端" prop="channels">
            <el-checkbox-group v-model="objFormEmailSingle.channels">
                <el-checkbox v-for="(item,idx) in aChannels" :label="item.id" :key="idx">{{item.name}}</el-checkbox>
            </el-checkbox-group>
        </el-form-item>
        <el-form-item label="礼包类型">
            <el-radio-group v-model="objFormEmailSingle.type" prop="type">
                <p>
                    <el-radio label="1">普通类型</el-radio>
                    <span>1个礼包码，1个账号只能使用1次、该类礼包1个账号只能使用其中1个</span>
                </p>
                <p>
                    <el-radio label="2">万能类型</el-radio>
                    <span>1个礼包码，N个账号只能使用1次、该类礼包1个账号只能使用其中1个</span>
                </p>
                <p>
                    <el-radio label="3">福利类型</el-radio>
                    <span>1个礼包码，1个账号只能使用1次、该类礼包1个账号只能使用其中N个</span>
                </p>
            </el-radio-group>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="submitForm('objFormEmailSingle')">创建礼包码</el-button>
        </el-form-item>
    </el-form>
</div>
</template>

<script>
export default {
    name: "coupon-createCode",
    props: {
        oriTab: {
            type: String,
            default: "create"
        }
    },
    data() {
        var checkServer = (rule, value, callback) => {
            if (!value) {
                return callback(new Error("至少选择一个服务器"));
            } else {
                callback();
            }
        };
        return {
            aCouponId: [],
            // 渠道
            aChannels: [{
                    id: 1,
                    name: "A服"
                },
                {
                    id: 2,
                    name: "B服"
                },
                {
                    id: 3,
                    name: "C服"
                }
            ],
            aPlatform: [{
                    id: 101,
                    name: "平台1"
                },
                {
                    id: 102,
                    name: "平台2"
                }
            ],
            objFormEmailSingle: {
                platform: "",
                name: "",
                describe: "",
                num: 0,
                minLevel: "",
                maxLevel: "",
                start: "",
                end: "",
                title: "",
                content: "",
                couponId: "",
                channels: [],
                type: "1"
            },
            rules: {
                platform: [{
                    required: true,
                    message: "请选择平台",
                    trigger: "change"
                }],
                name: [{
                    required: true,
                    message: "请输入名称",
                    trigger: "blur"
                }],
                describe: [{
                    required: true,
                    message: "请输入分类信息",
                    trigger: "blur"
                }],
                num: [{
                    required: true,
                    message: "请输入数量"
                }],
                minLevel: [{
                    required: true,
                    message: "请输入使用最低等级",
                    trigger: "blur"
                }],
                maxLevel: [{
                    required: true,
                    message: "请输入使用最高等级",
                    trigger: "blur"
                }],
                start: [{
                    required: true,
                    message: "请选择时间",
                    trigger: "change"
                }],
                end: [{
                    required: true,
                    message: "请选择时间",
                    trigger: "change"
                }],
                title: [{
                    required: true,
                    message: "请输入邮件标题",
                    trigger: "blur"
                }],
                content: [{
                    required: true,
                    message: "请输入邮件内容",
                    trigger: "blur"
                }],
                couponId: [{
                    required: true,
                    message: "请选择礼包",
                    trigger: "change"
                }],
                channels: [{
                    required: true,
                    message: "请选择分发客户端",
                    trigger: "change"
                }],
                type: [{
                    required: true,
                    message: "请选择礼包类型",
                    trigger: "change"
                }]
            }
        };
    },
    watch: {
        oriTab: {
            handler() {
                if (this.oriTab == "createCode") {
                    this.fetchPlatformList();
                    this.fetchChannelList();
                    this.fetchCouponIdList();
                }
            }
        }
    },
    methods: {
        //   提交
        submitForm(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    let objCopy = JSON.parse(JSON.stringify(this.objFormEmailSingle));
                    this.$fetch
                        .post(API.coupon.create_coupon_code, objCopy)
                        .then(response => {
                            let res = response.data;
                            if (res.status == 200) {
                                this.$refs[formName].resetFields();
                                this.$message.success(`创建成功 礼包批次号为：【${res.data.sequenceId}】`)
                            } else {
                                this.$message.error(res.desc);
                            }
                        });
                } else {
                    return false;
                }
            });
        },
        fetchPlatformList() {
            this.$fetch.post(API.coupon.platform_list).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.aPlatform = res.data;
                } else {
                    this.$message.error(res.desc);
                }
            });
        },
        fetchChannelList() {
            this.$fetch.post(API.anno.channels).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.aChannels = res.data;
                } else {
                    this.$message.error(res.desc);
                }
            });
        },
        fetchCouponIdList() {
            this.$fetch.post(API.coupon.coupon_id_list).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.aCouponId = res.data;
                } else {
                    // this.$message.error(res.desc);
                }
            });
        }
    },
    mounted() {
        this.fetchPlatformList();
        this.fetchChannelList();
        this.fetchCouponIdList();
    }
};
</script>
